import React from "react";
import Navbar from "../../components/navBar";
import "./about.css";
import { useTranslation } from "react-i18next";

const About = () => {
  const { t } = useTranslation();
  return (
    <>
      <Navbar />
      <div className="about">
        <p>{t("about_page_text")}</p>
        <br />
        <br />
        
      </div>
    </>
  );
};

export default About;
